// Fonction pour obtenir une analyse en fonction de la requte
export const getAnalysis = (query) => {
  const lowerQuery = query.toLowerCase();
  
  // Recherche par mots-cls
  if (lowerQuery.includes('vhicule') || lowerQuery.includes('lectrique') || lowerQuery.includes('voiture')) {
    return mockAnalyses["march des vhicules lectriques"];
  }
  
  if (lowerQuery.includes('e-commerce') || lowerQuery.includes('commerce en ligne') || lowerQuery.includes('vente en ligne')) {
    return mockAnalyses["march du e-commerce en france"];
  }
  
  // Retourne l'analyse par dfaut si aucune correspondance
  return mockAnalyses["default"];
};

